<template>
    <div class="cards">
        <ul>
            <li>
                <div class="top flex">
                    <img :src="cardInfo.logoUrl" alt="">
                    <h4>{{cardInfo.bankName}}</h4>
                </div>
                <div class="card-no">
                    {{cardInfo.cardNo}}
                </div>
            </li>
        </ul>

        <div class="btn-block">
            <cube-button class="btn-main re-btn" @click="newCard" :primary="true">我要换卡</cube-button>
        </div>
        <div class="tip">
            <router-link to="/bankList">下款必看！可下款的银行卡列表 ></router-link>
        </div>
    </div>
</template>

<script>
import { fetch, fetchHeader } from '../api/axios.js'
export default {
    data() {
        return {
            cardInfo: {
                bankName: '',
                cardNo: '',
                logoUrl: ''
            }
        }
    },
    methods: {
        async getData() {
            const data = await fetchHeader('/auth/card/cardList', {}, 'post')
            console.log(data)
            if (data.code === 0 && data.data.length > 0) {
                const info = data.data[0]
                Object.keys(this.cardInfo).map(item => {
                    this.cardInfo[item] = info[item]
                })
            }
        },
        newCard() {
            this.$router.push('bankAuth?from=bankCardList')
        }
    },
    beforeCreate() {
        document.title = '银行卡信息'
    },
    created() {
        this.getData()
    }
}
</script>

